@-webkit-keyframes rankingAni {
0% {
    top: 0rem;
}
5% {
    top: -0.26667rem;
}
10% {
    top: 0rem;
}
12% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    top: 0rem;
}
15% {
    -webkit-transform: rotate(-20deg);
    transform: rotate(-20deg);
    top: 0rem;
}
21% {
    -webkit-transform: rotate(20deg);
    transform: rotate(20deg);
    top: 0rem;
}
24% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    top: 0rem;
}
100% {
    top: 0rem;
}
}
@keyframes rankingAni {
0% {
    top: 0rem;
}
5% {
    top: -0.26667rem;
}
10% {
